@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css?family=Nunito);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  color: white;
  font-family: 'Nunito', sans-serif;
  background: -webkit-linear-gradient(top, rgba(16, 94, 158, 1) 0%, rgba(31, 56, 79, 1) 100%);

  -webkit-align-items: center;

          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  overflow: hidden;

  -webkit-transition: 1ms;

  transition: 1ms;
}

.App {
  height: 100vh;
  display: grid;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 30s linear;
          animation: App-logo-spin infinite 30s linear;
  height: 35vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


/* Header and Footer */
.Main-Header {
  text-align: center;
  background: -webkit-linear-gradient(top, rgba(16, 94, 158, 1) 0%, rgba(31, 56, 79, 1) 100%);
}

.Main-Footer {
  text-align: center;
  position: relative;
  width: -webkit-fill-available;
  bottom: 0%;
  padding-top: 2.5vh;
  background: -webkit-linear-gradient(top, rgba(16, 94, 158, 1) 0%, rgba(31, 56, 79, 1) 100%);
}
audio {
  display: none; }

.root {
  overflow: hidden; }

.Player {
  background: #182530;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 5px 10px -5px #121212;
  height: 100vh; }
  .Player .Background {
    width: 150%;
    height: 150%;
    position: fixed;
    overflow: hidden;
    top: -25%;
    left: -25%;
    background: url(https://funkadelphia.files.wordpress.com/2012/09/odesza-summers-gone-lp.jpg);
    background-size: cover;
    background-position: center center;
    opacity: .2;
    -webkit-filter: blur(10px);
            filter: blur(10px); }
  .Player .Title {
    width: 300px;
    margin: 50px auto;
    text-align: center;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: .1em;
    position: relative; }
  .Player .Header {
    display: -webkit-flex;
    display: flex; }
    .Player .Header .fa.back {
      -webkit-align-self: center;
              align-self: center;
      padding-left: 5%;
      padding-right: 5%; }
  .Player .Artwork {
    width: 69vmin;
    height: 69vmin;
    background: url(https://funkadelphia.files.wordpress.com/2012/09/odesza-summers-gone-lp.jpg);
    background-size: cover;
    background-position: center center;
    border-radius: 4px;
    margin: auto;
    box-shadow: 0 5px 10px -5px rgba(18, 18, 18, 0.25);
    position: relative; }
  .Player .TrackInformation {
    width: 300px;
    margin: 30px auto;
    text-align: center;
    position: relative; }
    .Player .TrackInformation .Name {
      font-size: 24px;
      margin-bottom: 0px;
      font-weight: 300; }
    .Player .TrackInformation .Artist {
      font-size: 16px;
      margin-bottom: 10px;
      font-weight: 500; }
    .Player .TrackInformation .Album {
      font-size: 12px;
      opacity: .5; }
  .Player .Scrubber {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 25%;
    opacity: .3;
    -webkit-transition: opacity .25s ease;
    transition: opacity .25s ease;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px; }
    .Player .Scrubber:active {
      opacity: 1; }
    .Player .Scrubber .Scrubber-Progress {
      background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0, 110, 153, 0)), color-stop(100%, #006e99));
      background: -webkit-linear-gradient(top, rgba(0, 110, 153, 0) 0%, #006e99 100%);
      background: linear-gradient(to bottom, rgba(0, 110, 153, 0) 0%, #006e99 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff4800', endColorstr='#ff4800', GradientType=0);
      height: 100%;
      width: 0%;
      -webkit-transition: width .25s ease;
      transition: width .25s ease;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px; }
      .Player .Scrubber .Scrubber-Progress.play {
        height: 100%;
        -webkit-transition: height .2s ease, opacity 1s ease;
        transition: height .2s ease, opacity 1s ease; }
      .Player .Scrubber .Scrubber-Progress.pause {
        height: 500%;
        -webkit-transition: height .2s ease, opacity 1s ease;
        transition: height .2s ease, opacity 1s ease; }
  .Player .Timestamps {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    box-sizing: border-box;
    padding: 20px;
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    z-index: 2;
    width: 100%; }
    .Player .Timestamps .Time {
      font-size: 12px; }
  .Player .Controls {
    position: absolute;
    bottom: 8%;
    pointer-events: none;
    margin: auto;
    left: 0;
    right: 0; }
    .Player .Controls .Button {
      height: 75px;
      width: 75px;
      border: 2px solid rgba(255, 255, 255, 0.5);
      box-sizing: border-box;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      border-radius: 75px;
      box-shadow: 0 5px 10px 0px rgba(18, 18, 18, 0.125);
      margin: auto;
      pointer-events: all; }
      .Player .Controls .Button:hover {
        -webkit-transition: all 0.5s ease;
        background: #FFFFFF; }
        .Player .Controls .Button:hover .fa {
          color: #006e99;
          opacity: 1; }
      .Player .Controls .Button:active {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
        background: #FFFFFF; }
        .Player .Controls .Button:active .fa {
          color: #006e99;
          opacity: 1; }
      .Player .Controls .Button .fa {
        color: #FFFFFF;
        opacity: .8;
        font-size: 24px; }
        .Player .Controls .Button .fa.play {
          opacity: 1;
          -webkit-transition: all .5s ease;
          transition: all .5s ease; }
        .Player .Controls .Button .fa.pause {
          opacity: .8;
          -webkit-transition: all .5s ease;
          transition: all .5s ease; }



